import { CSSProperties } from "react";

export const mergeStyle = (...args: Array<CSSProperties | undefined>): CSSProperties | undefined => {

  const styles = [...args].filter(a => a !== undefined);

  if (styles.length === 0) {
    return undefined;
  } else if (styles.length === 1) {
    return styles[0];
  } else {
    const style: CSSProperties = {};
    for (const arg of args) {
      if (arg !== undefined) {
        Object.assign(style, arg);
      }
    }
    return style;
  }
};